body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1c3334;
  color: white;
  text-align: center;
  
}
.background{
  position: absolute;
  width: 100%;
  top: 7%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hideDesktop{
display:block;
}
.hideMobile{
  display:none;
  }
.body{
  height: 100vh;
  width: 100%;
  /* margin-top:40%; */
  position: absolute;
}

.headInner{
  height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.head{
  width: 100%;
  background: #2e151B;
  height: 160px;
  text-align: center;
  vertical-align: middle;
  top: 30%;
  position: absolute;
  font-size: 32pt;
}
.buttonBig{
  bottom: 30%;
  width: 100%;
  position: absolute;
}
.mainButton{
  padding: 15px;
    /* margin: 50px; */
    border-radius: 5px;
    background: #2e151B;
    border: #2e151B solid 4px;
    color: white;
    font-size: 16pt;
    transition: .7s;
}
.mainButton:hover{
  /* color: #1c3334; */
  background: #2e151B80;
}

@media only screen and (max-width: 700px){
  .buttonBig{
    bottom:20%
  }
  .hideDesktop{
    display:none;
    }
    .hideMobile{
      display:block;
      }
  .head{
    top: 20%;
  }


}